import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			list: [],
			loadingText: '正在加载...',
			hasmore: true,
			curpage: 1,
			//控制滑动效果
			theIndex: null,
			oldIndex: null,
			isStop: false
		}
	},
	computed: mapState(['hasLogin', 'userInfo']),
	onLoad() {
		console.log('页面加载');
		this.loadData();
	},
	onReachBottom() {
		console.log('下拉加载...');
		this.curpage++;
		this.loadData();
	},
	onPageScroll() {
		console.log('页面滚动...');
	},
	onPullDownRefresh() {
		console.log('上拉刷新...');
		uni.stopPullDownRefresh();
	},
	methods: {
		async loadData() {
			var _this = this;
			console.log('===userInfo===' + this.hasLogin);
			console.log(this.userInfo);
			console.log(this.userInfo.token);
			if (!this.hasLogin) {
				uni.navigateTo({
					url: '../login',
				})
			} else {
				if (!this.hasmore) {
					this.loadingText = '暂无更多信息';
					return false;
				}
				_this.$Request.post('member_message/systemmsg', {
					curpage: _this.curpage
				}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.hasmore = res.hasmore;
						console.log(_this.hasmore);
						let _list = res.datas.list;
						for (var i = 0; i < _list.length; i++) {
							_this.list.push(_list[i]);
						}
						if (!_this.hasmore) {
							_this.loadingText = '暂无更多信息';
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: res.datas.error,
							duration: 2000
						});
						uni.navigateTo({
							url: '../login',
						});
					}
				});
			}
		},
		toPage: function(url) {
			uni.navigateTo({
				url: url,
			})
		},
		//控制左滑删除效果-begin
		touchStart(index, event) {
			console.log('====touchStart====');
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			console.log('====touchMove====');
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},
		touchEnd(index, $event) {
			console.log('====touchEnd====');
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end
		deleteOrder(msgid) {
			var _this = this;
			_this.$Request.post('member_message/sysmsgdel', {
				msgid: msgid
			}).then(res => {
				console.log(res);
				if (res.code == 200) {
					console.log(res);
					let len = _this.list.length;
					for (let i = 0; i < len; i++) {
						if (msgid == _this.list[i].message_id) {
							_this.list.splice(i, 1);
							break;
						}
					}
					_this.oldIndex = null;
					_this.theIndex = null;
				} else {
					uni.showToast({
						icon: 'none',
						title: res.datas.error,
						duration: 2000
					});
				}
			});

		},
		toBack: function() {
			uni.switchTab({
				url: "../home",
				success(res) {
					const currentPages = getCurrentPages()
					console.log(currentPages[0]);
					currentPages[0].onLoad({
						...currentPages[0].options
					})
				}
			})

		}

	}
}
